<template>
  <div>
    <avue-crud ref="crud" :option="myOption" :data="list">
      <template slot="url">
        <div><a></a> / <a></a></div>
      </template>
      <template slot="menu">
        <div></div>
      </template>
    </avue-crud>
  </div>
</template>
<script>
/**
 * 
 *   
 *    
 *                
 * block 
 */

export default {
  name: 'item-attachment',
  props: {
    list: {
      type: Array,
      default: () => {
        return [];
      }
    },
    setting: {
      type: Array,
      default: () => {
        return [];
      }
    },
    option: {
      type: Object,
      default: () => {
        return {
          addBtn: true,
          editBtn: false,
          columnBtn: false,
          filterBtn: false,
          page: false,
          column: [
            { label: '', prop: 'label' },
            { label: '', prop: 'desc' },
            { label: '', prop: 'url', type: 'upload', slot: true }
          ]
        };
      }
    }
  },
  data() {
    return {
      tableObj: {},
      tableData: {}
    };
  },
  mounted() {
    console.log('tab');
  },
  computed: {
    myOption() {
      if (this.setting && this.setting.data && this.setting.data.fieldData) {
        let setting = this.setting.data.fieldData;
        let addBtn = !(!setting.addable || setting.addable === 'false');
        let option = this.option;
        option.addBtn = addBtn;
        option.delBtn = addBtn;
        return option;
      }
      return {};
    }
  },
  methods: {
    listRowUpdate(row, index, done, loading) {
      // 
      this.$set(this.ProviderData.tableData, index, row);
      loading();
      this.$emit('change', this.ProviderData);
      done();
    },
    listRowSave(payload, done) {
      this.ProviderData.tableData.push(payload);
      this.$emit('change', this.ProviderData);
      done();
    },
    listRowDel(payload) {
      this.ProviderData.tableData.splice(payload.index, 1);
    }
  }
};
</script>
<style scoped></style>
