export function isValidateUsername(str) {
  const validMap = ['admin', 'user'];
  return validMap.indexOf(str.trim()) >= 0;
}

/* uri */
export function validateURL(textVal) {
  const urlRegex = /^(https?|ftp):\/\/([a-zA-Z0-9.-]+(:[a-zA-Z0-9.&%$-]+)*@)*((25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9][0-9]?)(\.(25[0-5]|2[0-4][0-9]|1[0-9]{2}|[1-9]?[0-9])){3}|([a-zA-Z0-9-]+\.)*[a-zA-Z0-9-]+\.(com|edu|gov|int|mil|net|org|biz|arpa|info|name|pro|aero|coop|museum|[a-zA-Z]{2}))(:[0-9]+)*(\/($|[a-zA-Z0-9.,?'\\+&%$#=~_-]+))*$/;
  return urlRegex.test(textVal);
}
/**
 * 
 * @param {*} s
 */
export function isEmail(s) {
  return /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+((.[a-zA-Z0-9_-]{2,3}){1,2})$/.test(s);
}

/**
 * 
 * @param {*} s
 */
export function isMobile(s) {
  return /^1[0-9]{10}$/.test(s);
}

/**
 * 
 * @param {*} s
 */
export function isPhone(s) {
  return /^([0-9]{3,4}-)?[0-9]{7,8}$/.test(s);
}

/**
 * URL
 * @param {*} s
 */
export function isURL(s) {
  return /^http[s]?:\/\/.*/.test(s);
}

/*  */
export function validateLowerCase(str) {
  const reg = /^[a-z]+$/;
  return reg.test(str);
}

/*  */
export function validateUpperCase(str) {
  const reg = /^[A-Z]+$/;
  return reg.test(str);
}

/*  */
export function validateAlphabets(str) {
  const reg = /^[A-Za-z]+$/;
  return reg.test(str);
}
/* padpc */
export const validatePc = function() {
  const userAgentInfo = navigator.userAgent;
  const Agents = ['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'];
  let flag = true;
  for (var v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false;
      break;
    }
  }
  return flag;
};
/**
 * validate email
 * @param email
 * @returns {boolean}
 */
export function validateEmail(email) {
  const re = /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
  return re.test(email);
}

/**
 * 
 */
export function cardId(code) {
  let list = [];
  let result = true;
  let msg = '';
  var city = {
    11: '',
    12: '',
    13: '',
    14: '',
    15: '',
    21: '',
    22: '',
    23: ' ',
    31: '',
    32: '',
    33: '',
    34: '',
    35: '',
    36: '',
    37: '',
    41: '',
    42: ' ',
    43: '',
    44: '',
    45: '',
    46: '',
    50: '',
    51: '',
    52: '',
    53: '',
    54: ' ',
    61: '',
    62: '',
    63: '',
    64: '',
    65: '',
    71: '',
    81: '',
    82: '',
    91: ' '
  };
  if (!validateNull(code)) {
    if (code.length === 18) {
      if (!code || !/(^\d{18}$)|(^\d{17}(\d|X|x)$)/.test(code)) {
        msg = '';
      } else if (!city[code.substr(0, 2)]) {
        msg = '';
      } else {
        // 18
        code = code.split('');
        // (aiWi)(mod 11)
        // 
        var factor = [7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2];
        // 
        var parity = [1, 0, 'X', 9, 8, 7, 6, 5, 4, 3, 2, 'x'];
        var sum = 0;
        var ai = 0;
        var wi = 0;
        for (var i = 0; i < 17; i++) {
          ai = code[i];
          wi = factor[i];
          sum += ai * wi;
        }
        if (parity[sum % 11] !== code[17]) {
          msg = '';
        } else {
          result = false;
        }
      }
    } else {
      msg = '18';
    }
  } else {
    msg = '';
  }
  list.push(result);
  list.push(msg);
  return list;
}
/**
 * 
 */
export function isValidateMobile(phone) {
  let list = [];
  let result = true;
  let msg = '';
  var isPhone = /^1[3456789]\d{9}$/;
  // 134 |1349[0-9]{7}181,14517[678]
  if (!validateNull(phone)) {
    if (phone.length === 11) {
      if (!isPhone.test(phone)) {
        msg = '';
      } else {
        result = false;
      }
    } else {
      msg = ''; // 11
    }
  } else {
    msg = '';
  }
  list.push(result);
  list.push(msg);
  return list;
}
/**
 * 
 */
export function validateName(name) {
  var regName = /^[\u4e00-\u9fa5]{2,4}$/;
  if (!regName.test(name)) {
    return false;
  }
  return true;
}
/**
 * 
 */
export function validateNum(num, type) {
  let regName = /[^\d.]/g;
  if (type === 1) {
    if (!regName.test(num)) {
      return false;
    }
  } else if (type === 2) {
    regName = /[^\d]/g;
    if (!regName.test(num)) {
      return false;
    }
  }
  return true;
}
/**
 * 
 */
export function validateDecimal(num, type) {
  let regName = /[^\d.]/g;
  if (type === 1) {
    if (!regName.test(num)) {
      return false;
    }
  } else if (type === 2) {
    regName = /[^\d.]/g;
    if (!regName.test(num)) {
      return false;
    }
  }
  return true;
}
/**
 * 
 */
export function validateNull(val) {
  if (typeof val === 'boolean') {
    return false;
  }
  if (typeof val === 'number') {
    return false;
  }
  if (val instanceof Array) {
    if (val.length === 0) {
      return true;
    }
  } else if (val instanceof Object) {
    if (JSON.stringify(val) === '{}') {
      return true;
    }
  } else {
    if (val === 'null' || val == null || val === 'undefined' || val === undefined || val === '') {
      return true;
    }
    return false;
  }
  return false;
}

/**
 * 
 * @param rule
 * @param value
 * @param callback
 */
export const validateSpace = (rule, value, callback) => {
  var reg = /\s/;
  if (reg.test(value)) {
    callback(new Error(''));
  } else {
    callback();
  }
};

export const validateColumn = (column) => {
  function vaild(option = {}, ele = '') {
    const result = option[ele] + '' || '';
    return !result || ele.includes('$');
  }
  column.forEach((ele) => {
    Object.keys(ele).forEach((key) => {
      if (vaild(ele, key)) {
        delete ele[key];
      }
    });
  });
  return column;
};
